
    <template>
      <section class="market-doc-container">
        <h2>Button 按钮</h2>
<h3>Examples 基本示例</h3>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;mku-button type=&quot;primary&quot;&gt;Primary&lt;/mku-button&gt;
&lt;mku-button&gt;Default&lt;/mku-button&gt;
&lt;mku-button type=&quot;dashed&quot;&gt;Dashed&lt;/mku-button&gt;
&lt;mku-button type=&quot;danger&quot;&gt;Danger&lt;/mku-button&gt;
&lt;mku-button type=&quot;link&quot;&gt;Link-Button&lt;/mku-button&gt;
</code></pre></template></mku-example><h3>Size 按钮尺寸</h3>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;mku-button size=&quot;large&quot;&gt;large&lt;/mku-button&gt;
&lt;mku-button&gt;default&lt;/mku-button&gt;
&lt;mku-button size=&quot;small&quot;&gt;small&lt;/mku-button&gt;
</code></pre></template></mku-example><h3>Icon 图标</h3>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;mku-button type=&quot;primary&quot; icon=&quot;search&quot;&gt;搜索&lt;/mku-button&gt;
&lt;mku-button icon=&quot;download&quot;&gt;下载&lt;/mku-button&gt;
&lt;mku-button type=&quot;danger&quot; icon=&quot;delete&quot;&gt;删除&lt;/mku-button&gt;
</code></pre></template></mku-example><h3>Loading 加载中</h3>
<p>加载过程中再次点击，不会触发click事件</p>
<mku-example>
        
        <template slot="source"><element-demo3 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;mku-button type=&quot;primary&quot; :loading=&quot;isLoading1&quot;&gt;加载中&lt;/mku-button&gt;
&lt;mku-button
  icon=&quot;download&quot;
  :loading=&quot;isLoading2&quot;
  @click=&quot;handleClick&quot;
&gt;
  {{isLoading2 ? '下载中' : '立即下载'}}
&lt;/mku-button&gt;

&lt;script&gt;
  export default {
    data () {
      return {
        isLoading1: true,
        isLoading2: false
      }
    },
    methods: {
      handleClick () {
        this.isLoading2 = true
        setTimeout(() =&gt; {
          this.isLoading2 = false
        }, 2000)
      }
    }
  }
&lt;/script&gt;
</code></pre></template></mku-example><h3>Block 块级按钮</h3>
<p>块级按钮将自适应父容器尺寸。</p>
<mku-example>
        
        <template slot="source"><element-demo4 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;mku-button type=&quot;primary&quot; block&gt;立即提交&lt;/mku-button&gt;
&lt;br /&gt;
&lt;mku-button block&gt;Default&lt;/mku-button&gt;
&lt;br /&gt;
&lt;mku-button type=&quot;dashed&quot; block&gt;Dashed&lt;/mku-button&gt;
&lt;br /&gt;
&lt;mku-button type=&quot;danger&quot; block&gt;危险操作&lt;/mku-button&gt;
&lt;br /&gt;
&lt;mku-button type=&quot;link&quot; block&gt;Link-Button&lt;/mku-button&gt;
</code></pre></template></mku-example><h3>跳转外部连接</h3>
<mku-example>
        
        <template slot="source"><element-demo5 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;mku-button type=&quot;primary&quot; href=&quot;https://www.baidu.com&quot;&gt;
  在当前页面打开百度网
&lt;/mku-button&gt;

&lt;mku-button href=&quot;https://www.baidu.com&quot; target=&quot;_blank&quot;&gt;
  新开一个页面打开百度网
&lt;/mku-button&gt;

&lt;mku-button type=&quot;link&quot; href=&quot;https://www.baidu.com&quot; target=&quot;customName&quot;&gt;
  在自定义的窗口，打开百度网
&lt;/mku-button&gt;
</code></pre></template></mku-example><h3>Button Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left">示例</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">type</td>
<td style="text-align:left">按钮类型</td>
<td style="text-align:left">String</td>
<td style="text-align:left"><code>primary</code>、<code>default</code>、<code>dashed</code>、<code>danger</code>、<code>link</code></td>
<td style="text-align:left"><code>default</code></td>
<td style="text-align:left"><code>danger</code></td>
</tr>
<tr>
<td style="text-align:left">block</td>
<td style="text-align:left">是否成为块级按钮，会自适应铺满父容器</td>
<td style="text-align:left">Boolean</td>
<td style="text-align:left"><code>true</code>、<code>false</code></td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left"><code>true</code></td>
</tr>
<tr>
<td style="text-align:left">size</td>
<td style="text-align:left">按钮尺寸</td>
<td style="text-align:left">String</td>
<td style="text-align:left"><code>large</code>、<code>default</code>、<code>small</code></td>
<td style="text-align:left"><code>default</code></td>
<td style="text-align:left">20</td>
</tr>
<tr>
<td style="text-align:left">icon</td>
<td style="text-align:left">按钮图标</td>
<td style="text-align:left">String</td>
<td style="text-align:left">所有组件库支持的icon</td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>search</code></td>
</tr>
<tr>
<td style="text-align:left">loading</td>
<td style="text-align:left">loading状态，加载过程中禁止重复点击</td>
<td style="text-align:left">Boolean</td>
<td style="text-align:left"><code>true</code>、<code>false</code></td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left"><code>true</code></td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">是否禁用</td>
<td style="text-align:left">Boolean</td>
<td style="text-align:left"><code>true</code>、<code>false</code></td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left"><code>true</code></td>
</tr>
<tr>
<td style="text-align:left">href</td>
<td style="text-align:left">按钮作为链接跳转，功能类似于a标签的href属性</td>
<td style="text-align:left">String</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>https://www.baidu.com</code></td>
</tr>
<tr>
<td style="text-align:left">target</td>
<td style="text-align:left">按钮作为链接跳转的方式，类似于a标签的target属性</td>
<td style="text-align:left">String</td>
<td style="text-align:left"><code>_blank</code> 、<code>_parent</code> 、<code>_self</code> 、<code>_top</code> 、其他自定义的name字符串；</td>
<td style="text-align:left"><code>_blank</code></td>
<td style="text-align:left"><code>_self</code></td>
</tr>
</tbody>
</table>
<h3>Button Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">click</td>
<td style="text-align:left">点击按钮时触发</td>
<td style="text-align:left">-</td>
</tr>
</tbody>
</table>

